import NavItem from "./NavItem";
import boltImg from "./../../assets/images/bolts.svg";
import beamImg from "./../../assets/images/beam.svg";
import eyeletImg from "./../../assets/images/eyelet.svg";
import springImg from "./../../assets/images/spring-icon.svg";
import tolerancesImg from "./../../assets/images/tolerances.svg";

const Links = ({ effect }) => {
  return (
    <>
      <NavItem link="/LashingRing" text=" " effect={effect} img={eyeletImg} />
      <NavItem link="/Beams" text=" " effect={effect} img={beamImg} />
      <NavItem link="/BoltGroup" text="  " effect={effect} img={boltImg} />
      <NavItem link="/Spring" text=" " effect={effect} img={springImg} />
      <NavItem link="/Tolerances" text="  " effect={effect} img={tolerancesImg} />
    </>
  );
};

export default Links;
