import stl from "./Spring.module.css";
import { useDispatch, useSelector } from "react-redux";
import {
  calculateSpring,
  setSpringType,
  calculateF3,
  setF3Unit,
  setDUnit,
  setF0Unit,
  setCUnit,
  setS1Unit,
  setL2Unit,
  setL1Unit,
  setL0Unit,
  setL3Unit,
  setS3Unit,
  setS2Unit,
  setTUnit,
  setLUnit,
  setMUnit,
} from "../../../redux/springSlice";
import Units from "../../Units/Units";
import { kgUnitArray, kgsUnitArray, meterUnitArray, nmmUnitArray } from "../../Units/unitArrays";
import Spring from "./Spring";

let paramsSpringTypeArray = [
  { value: 1, name: " " },
  { value: 2, name: " " },
];

const SpringContainer = () => {
  const { springType, F0, F3, c, n, D, s1, s2, s3, l0, l1, l2, l3, t, l, m } = useSelector(
    (state) => state.spring
  );

  const dispatch = useDispatch();

  function changeSpringType(type) {
    dispatch(setSpringType(type));
  }

  function calculate() {
    dispatch(calculateSpring());
  }

  function onClickchangeSpringType(param) {
    changeSpringType(param);
    dispatch(calculateSpring());
  }

  function buttonSpringTypeSelect(img, param) {
    return (
      <div
        className={`${stl.buttonSelect} ${
          springType === param.value ? stl.buttonSelectActive : null
        }`}
        onClick={() => onClickchangeSpringType(param)}
      >
        <img src={img} alt="" />
        <p>{param.name}</p>
      </div>
    );
  }

  function typeText() {
    if (springType === 1) {
      return paramsSpringTypeArray[0].name;
    } else if (springType === 2) {
      return paramsSpringTypeArray[1].name;
    }
  }

  function resultText() {
    if (springType === 1) {
      return [
        "   ",
        "  ",
        "   ",
        " - ",
      ];
    } else if (springType === 2) {
      return [
        "   ",
        "  ",
        "   ",
        " - ",
      ];
    } else {
      return "";
    }
  }

  const results1 = [
    {
      id: 1,
      name: "    ",
      value: F3.value,
      unit: (
        <Units
          unitArr={kgsUnitArray}
          changeUnit={setF3Unit}
          currentUnit={F3.unit}
          calculateFn={calculateF3}
        />
      ),
    },
  ];

  const results2 = [
    {
      id: 1,
      name: "  ",
      value: F0.value,
      unit: (
        <Units
          unitArr={kgsUnitArray}
          changeUnit={setF0Unit}
          currentUnit={F0.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 2,
      name: " ",
      value: c.value,
      unit: (
        <Units
          unitArr={nmmUnitArray}
          changeUnit={setCUnit}
          currentUnit={c.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 3,
      name: "   ",
      value: D.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setDUnit}
          currentUnit={D.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    { id: 4, name: " ", value: n, unit: "" },
    {
      id: 5,
      name: "  ",
      value: s1.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setS1Unit}
          currentUnit={s1.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 6,
      name: " ",
      value: s2.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setS2Unit}
          currentUnit={s2.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 7,
      name: " ",
      value: s3.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setS3Unit}
          currentUnit={s3.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 8,
      name: resultText()[0],
      value: l3.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setL3Unit}
          currentUnit={l3.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 9,
      name: resultText()[1],
      value: l0.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setL0Unit}
          currentUnit={l0.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 10,
      name: resultText()[2],
      value: l1.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setL1Unit}
          currentUnit={l1.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 11,
      name: resultText()[3],
      value: l2.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setL2Unit}
          currentUnit={l2.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 12,
      name: " ",
      value: t.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setTUnit}
          currentUnit={t.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 13,
      name: " ",
      value: l.value,
      unit: (
        <Units
          unitArr={meterUnitArray}
          changeUnit={setLUnit}
          currentUnit={l.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
    {
      id: 14,
      name: " ",
      value: m.value,
      unit: (
        <Units
          unitArr={kgUnitArray}
          changeUnit={setMUnit}
          currentUnit={m.unit}
          calculateFn={calculateSpring}
        />
      ),
    },
  ];

  return (
    <Spring
      results1={results1}
      results2={results2}
      calculate={calculate}
      buttonSpringTypeSelect={buttonSpringTypeSelect}
      typeText={typeText}
      paramsSpringTypeArray={paramsSpringTypeArray}
    />
  );
};

export default SpringContainer;
